*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --col-bg-primary: #fdf0e9;
  --col-bg-light: #fff;
  --col-bg-middle: #ef6d58;
  --col-bg-dark: #28293e;
  --col-text-primary: #391400;
  --col-text-secondary: #fff;
  --color-text-inactive-dark: rgba(57, 20, 0, .64);
  --color-text-inactive-light: rgba(255, 255, 255, .64);
  --col-title-primary: #391400;
  --col-title-secondary: #fff;
  --col-accent-primary: #ef6d58;
  --col-accent-secondary: #b550f8;
  --col-btn-default: #fff;
  --col-btn-hover-dark: #391400;
  --col-btn-hover-light: #ef6d58;
  --col-btn-focus: #ef6d58;
  --col-btn-disabled: #000;
  --col-btn-bg-default-primary: #ef6d58;
  --col-btn-bg-default-secondary: #b550f8;
  --col-btn-bg-hover: #fff;
  --col-btn-bg-focus: rgba(255, 255, 255, .3);
  --col-btn-bg-disabled: #ababab;
  --border-btn-focus-light: 2px solid rgba(255, 255, 255, .3);
  --fill-icon: #fdf0e9;
  --col-bg-icon: #ef6d58;
  --col-bg-icon-hover: rgba(255, 255, 255, .3);
  --col-bg-icon-focus: transparent;
  --col-backdrop: rgba(0, 0, 0, .2);
  --border: 1px solid #f3d1bf;
  --cubic-function: cubic-bezier(.4, 0, .2, 1);
  --animation-time: 250ms;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  color: inherit;
  text-decoration: none;
}

.address {
  font-style: normal;
}

:focus {
  outline-style: none;
}

.no-scroll {
  overflow: hidden;
}

body {
  letter-spacing: .04em;
  color: var(--col-text-primary);
  font-family: Epilogue, sans-serif;
  font-size: 14px;
  line-height: 1.76;
}

a, button {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

.section {
  background-color: var(--col-bg-primary);
  padding-top: 64px;
  padding-bottom: 64px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-top: 56px;
    padding-bottom: 56px;
  }
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 48px;
    padding-bottom: 48px;
  }
}

.container {
  max-width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding-left: 23px;
  padding-right: 23px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.example {
  padding: 60px 15px;
  display: block;
}

.example--dark {
  background-color: var(--col-bg-dark);
  color: var(--col-text-secondary);
}

@keyframes slide-top {
  from {
    transform: scale(.9);
  }

  to {
    transform: scale(1.1);
  }
}

@keyframes scaler {
  from {
    transform: scale(.8);
  }

  to {
    transform: scale(1);
  }
}

.scroll-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--col-btn-bg-default-primary);
  cursor: pointer;
  z-index: 10;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: slide-top .75s linear, scaler 1.5s infinite alternate both;
  display: none;
  position: fixed;
  bottom: 5vh;
  right: 30px;
}

.arrow-top {
  fill: #fff;
}

.btn {
  height: 48px;
  color: var(--col-btn-default);
  background-color: var(--col-btn-bg-default-primary);
  transition: color var(--animation-time) var(--cubic-function), background-color var(--animation-time) var(--cubic-function);
  border-style: none;
  border-radius: 6px;
  align-items: center;
  padding-left: 32px;
  padding-right: 32px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 900;
  line-height: 1;
  display: flex;
}

.btn:hover {
  color: var(--col-btn-hover-light);
  background-color: var(--col-btn-bg-hover);
}

.btn:focus {
  color: var(--col-btn-focus);
  background-color: var(--col-btn-bg-focus);
}

.btn:disabled {
  color: var(--col-btn-disabled);
  background-color: var(--col-btn-bg-disabled);
  cursor: inherit;
}

.btn--transparent {
  height: 42px;
  transition: color var(--animation-time) var(--cubic-function), border var(--animation-time) var(--cubic-function);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.btn--transparent:hover {
  color: var(--col-btn-hover);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0a8817;
}

.btn--transparent:focus {
  color: var(--col-btn-focus);
  fill: var(--col-btn-focus);
  background-color: rgba(0, 0, 0, 0);
}

.section-title {
  text-align: center;
  color: var(--col-title-primary);
  margin-bottom: 15px;
  font-family: Epilogue, sans-serif;
  font-size: 36px;
  font-weight: 800;
  line-height: 1.2;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 42px;
  }
}

@media screen and (min-width: 1200px) {
  .section-title {
    font-size: 56px;
  }
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  top: 10px;
  right: 15px;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: var(--col-bg-light);
  padding: 25px 45px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-wrap {
  height: calc(100% - 170px);
  flex-direction: column;
  justify-content: space-between;
  gap: 15%;
  display: flex;
}

.mobile-menu__item {
  padding: 15px 0;
}

.mobile-menu__link {
  letter-spacing: .02em;
  font-size: 40px;
  font-weight: 500;
  line-height: 1.17;
}

.contact-menu__item {
  padding: 15px 0;
}

.contact-menu__link--tel {
  letter-spacing: .02em;
  font-size: 34px;
  font-weight: 500;
  line-height: 1.17;
}

.contact-menu__link--mail {
  letter-spacing: .02em;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.menu-soc-list {
  gap: 20px;
  display: flex;
}

.menu-soc-list__item {
  align-items: center;
  display: flex;
  position: relative;
}

.menu-soc-list__link {
  letter-spacing: .02em;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.22;
}

.menu-soc-list__link:before {
  width: 1px;
  height: 100%;
  content: "";
  border-radius: 2px;
  position: absolute;
  bottom: 2px;
  left: -10px;
}

.latest-work {
  background-color: var(--col-bg-dark);
  padding-top: 92px;
  padding-bottom: 140px;
}

.latest-work__container {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.latest-work__uppertitle {
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: var(--col-accent-primary);
  margin-bottom: 20px;
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 2;
}

@media screen and (min-width: 768px) {
  .latest-work__uppertitle {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .latest-work__uppertitle {
    font-size: 16px;
  }
}

.latest-work__title {
  text-align: center;
  letter-spacing: -1px;
  color: var(--col-text-secondary);
  margin: 0 0 36px;
  font-family: Epilogue;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 1.14;
}

@media screen and (min-width: 768px) {
  .latest-work__title {
    font-size: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .latest-work__title {
    font-size: 56px;
  }
}

.latest-work__gallery {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.latest-work__filters {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px 60px;
  margin-bottom: 65px;
  display: flex;
}

.filter-btn {
  color: rgba(255, 255, 255, .64);
  cursor: pointer;
  transition: transform var(--cubic-function) 250ms;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  padding: 5px;
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 2;
  position: relative;
}

@media screen and (min-width: 768px) {
  .filter-btn {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
  .filter-btn {
    font-size: 16px;
  }
}

.filter-btn:hover, .filter-btn:focus {
  color: var(--col-btn-focus);
  text-shadow: 2px 2px 6px #191a30;
  transform: scale(1.1);
}

.latest-work__wrapper {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 64px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.latest-work__wrapper .img__wrapper.hide {
  display: none;
}

.latest-work__wrapper .img__wrapper.show {
  animation: animate .5s;
  display: flex;
}

@keyframes animate {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

.img__wrapper {
  max-width: 570px;
  min-width: 280px;
  width: 100%;
  height: 420px;
  box-shadow: 1px 1px 2px 0px var(--color-text-inactive-dark);
  border-radius: 20px;
  flex-basis: 100%;
  justify-content: center;
  align-items: center;
  display: none;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .img__wrapper {
    flex-basis: 48%;
  }
}

@media screen and (min-width: 1200px) {
  .img__wrapper {
    flex-basis: 48%;
  }
}

.img__wrapper:nth-child(-n+4) {
  display: flex;
}

.img__wrapper:hover, .img__wrapper:focus {
  box-shadow: 1px 1px 15px 0px var(--color-text-inactive-dark);
}

.img-title__wrapper {
  color: var(--col-text-primary);
  background-color: var(--col-bg-light);
  z-index: 1;
  border-radius: 19px;
  padding: 5px 16px 1px 15px;
  font-family: Epilogue;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  display: flex;
  position: absolute;
  bottom: 125px;
  left: 50px;
}

.img__name {
  text-transform: uppercase;
  z-index: 2;
  color: var(--col-text-secondary);
  font-family: Epilogue;
  font-size: 40px;
  font-style: normal;
  font-weight: 800;
  line-height: 48px;
  display: block;
  position: absolute;
  bottom: 50px;
  left: 50px;
}

.img {
  object-fit: cover;
  max-width: 100%;
  width: 570px;
  height: 100%;
  border: none;
  outline: none;
  transition: transform .25s;
  position: absolute;
  top: 0;
  left: 0;
}

.img:hover, .img:focus {
  transform: scale(1.1);
}

.showmore-btn {
  text-align: center;
  text-transform: uppercase;
  color: var(--col-btn-default);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(255, 255, 255, .294);
  border-radius: 6px;
  margin-left: auto;
  margin-right: auto;
  padding: 18px 30px;
}

.active {
  color: var(--col-btn-focus);
}

.all-count {
  text-align: center;
  color: rgba(255, 255, 255, .64);
  font-family: Epilogue;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.71;
  position: absolute;
  transform: translateY(-20%);
}

@media screen and (min-width: 768px) {
  .all-count {
    font-size: 10px;
  }
}

@media screen and (min-width: 1200px) {
