.faq__container {
  flex-wrap: wrap;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .faq__container {
    flex-wrap: nowrap;
  }
}

.faq__wrapper {
  flex-basis: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media screen and (min-width: 1200px) {
  .faq__wrapper {
    flex-basis: 44%;
  }
}

.faq__uppertitle {
  letter-spacing: .03px;
  text-transform: uppercase;
  color: var(--col-accent-primary);
  margin-bottom: 17px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 2;
}

.faq__title {
  color: #391400;
  margin-bottom: 19px;
  font-family: inherit;
  font-size: 38px;
  font-weight: 800;
  line-height: 1.2;
}

@media screen and (min-width: 768px) {
  .faq__title {
    margin-bottom: 25px;
    font-size: 38px;
  }
}

@media screen and (min-width: 1200px) {
  .faq__title {
    margin-bottom: 31px;
    font-size: 40px;
  }
}

.faq__text {
  color: var(--color-text-inactive-dark);
  margin-bottom: 32px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 2;
}

@media screen and (min-width: 768px) {
  .faq__text {
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .faq__text {
    margin-bottom: 29px;
    font-size: 16px;
  }
}

.faq__link {
  margin-bottom: 36px;
  font-size: 16px;
  font-weight: 400;
  line-height: 2;
}

.faq__link:hover, .faq__link:focus {
  text-decoration: underline;
}

.faq-article__wrapper {
  flex-direction: column;
  padding-top: 20px;
  display: flex;
}

.faq__article {
  max-width: 100%;
  border: var(--border);
  flex-basis: 49%;
  padding: 41px 28px 28px;
  transition: all .5s;
  display: block;
  position: relative;
}

.faq__article:first-child {
  border-radius: 6px 6px 0 0;
}

.faq__article:last-child {
  border-radius: 0 0 6px 6px;
}

@media screen and (min-width: 1200px) {
  .faq__article {
    width: 570px;
    max-height: 113px;
    padding: 40px 45px;
  }
}

.faq__article.animate {
  background-color: #fff;
  transition: all .5s;
  box-shadow: 0 32px 64px rgba(57, 20, 0, .04);
}

.question__btn-more, .question__btn-less {
  width: 32px;
  height: 32px;
  transform-origin: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  transition: transform .25s;
  display: block;
  position: absolute;
  top: 40px;
  right: 34px;
}

.question__btn-more:hover, .question__btn-more:focus, .question__btn-less:hover, .question__btn-less:focus {
  transform: scale(1.2);
}

.faq__icon-up, .faq__icon-down {
  fill: var(--col-accent-primary);
  animation: animate .5s;
}

.hide-btn {
  animation: animate .5s;
  display: none;
}

.article__title {
  color: var(--col-text-primary);
  margin-right: 35px;
  font-family: inherit;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.33333;
  display: block;
}

@media screen and (min-width: 480px) {
  .article__title {
    font-size: 24px;
  }
}

@media screen and (min-width: 1200px) {
  .article__title {
    font-size: 24px;
  }
}

.article-text__wrapper {
  max-height: 0;
  transition: all .5s;
  overflow: hidden;
}

.article__text {
  color: var(--color-text-inactive-dark);
  margin-top: 19px;
  font-size: 16px;
  font-weight: 400;
  line-height: 2;
}

@media screen and (min-width: 480px) {
  .article__text {
    font-size: 14px;
    line-height: 1.6;
