.btn {
  display: flex;
  align-items: center;

  border-style: none;
  font-family: inherit;

  height: 48px;
  padding-left: 32px;
  padding-right: 32px;
  border-radius: 6px;

  color: var(--col-btn-default);
  background-color: var(--col-btn-bg-default-primary);
  font-weight: 900;
  font-size: 14px;
  line-height: calc(14 / 14);

  transition: color var(--animation-time) var(--cubic-function),
    background-color var(--animation-time) var(--cubic-function);

  &:hover {
    color: var(--col-btn-hover-light);
    background-color: var(--col-btn-bg-hover);
  }

  &:focus {
    color: var(--col-btn-focus);
    background-color: var(--col-btn-bg-focus);
  }

  &:disabled {
    color: var(--col-btn-disabled);
    background-color: var(--col-btn-bg-disabled);
    cursor: inherit;
  }

  &--transparent {
    font-family: inherit;
    padding: 12px 34px;
    border-radius: 10px;
    height: 42px;

    font-weight: 600;
    font-size: 14px;
    line-height: calc(18 / 14);

    background-color: transparent;
    border: 2px solid #f8d24e;
    transition: color var(--animation-time) var(--cubic-function),
      border var(--animation-time) var(--cubic-function);

    &:hover {
      color: var(--col-btn-hover);
      border: 2px solid #0a8817;
      background-color: transparent;
    }

    &:focus {
      color: var(--col-btn-focus);
      background-color: transparent;
      fill: var(--col-btn-focus);
    }
  }
}
